import React, { useState } from 'react';
import './styles.css';
const App = () => {
  const [title, setTitle] = useState('Chips');
  const toggleName = () => {
    if (title === 'Chips') {
      setTitle('Pips');
    } else {
      setTitle('Chips');
    }
  };
  return (
    <div className="App">
      <h1>Hello CodeSandbox {title}</h1>
      <h2>Start editing to see some magic happen!</h2>
      <button onClick={toggleName}>Change Name</button>
    </div>
  );
};
export default App;
